import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';
import type { ITooltipCell } from '../../../../types';

export interface TitleSubtitleData {
    title: string;
    subtitle: string;
    tooltip?: string;
}

export interface ITitleSubtitleCellParams
    extends ICellRendererParams,
        ITooltipCell {
    title?: (data: any) => string;
    subtitle?: (data: any) => string;
}

/**
 * @category Table Cell
 * @description A cell component for displaying a title and subtitle.
 */
@Component({
    tag: 'at-title-subtitle-cell',
    shadow: false,
})
export class AtTitleSubtitleCell implements ICellRendererComp {
    @Element() el: HTMLElement;
    @State() params: ITitleSubtitleCellParams;
    @State() title: string = '';
    @State() subtitle: string = '';

    init(params: ITitleSubtitleCellParams): void {
        this.setParams(params);
    }

    refresh(params: ITitleSubtitleCellParams): boolean {
        this.setParams(params);
        return true;
    }

    getGui(): HTMLElement {
        return this.el;
    }

    private setParams(params: ITitleSubtitleCellParams) {
        this.params = params;
        if (params.title) {
            this.title = params.title(params.data);
        } else {
            this.title = params.data?.titleSubtitleCell?.title || '';
        }

        if (params.subtitle) {
            this.subtitle = params.subtitle(params.data);
        } else {
            this.subtitle = params.data?.titleSubtitleCell?.subtitle || '';
        }
    }

    render() {
        return (
            <Host class="flex h-full items-center">
                <at-tooltip
                    position="right"
                    disabled={!this.params?.generateTooltip}
                >
                    <div
                        class="flex h-full flex-col justify-center"
                        slot="tooltip-trigger"
                    >
                        <div class="truncate text-sm leading-normal">
                            {this.title}
                        </div>
                        <div class="text-med truncate text-xs leading-normal font-normal">
                            {this.subtitle}
                        </div>
                    </div>
                    {this.params?.generateTooltip && (
                        <span class="leading-normal">
                            {this.params.generateTooltip(this.params)}
                        </span>
                    )}
                </at-tooltip>
            </Host>
        );
    }
}
